import React, { useState } from "react";
import { Link, useLocation, Navigate } from "react-router-dom";
import Intro from "../components/Intro";
import SecretBoard from "../components/SecretBoard";
import Footer from "../components/Footer";

export default function SecretPage() {
  const [introComplete, setIntroComplete] = useState(false);

  const location = useLocation();
  // Check if the state is present, which means the user came from the button
  if (!location.state || !location.state.authorized) {
    return <Navigate to="/" />; // Redirect if not authorized
  }

  const handleIntroEnd = () => {
    setIntroComplete(true); // Show the main content after intro completes
  };

  return (
    <div>
      <Intro tag={"SECRET PAGE"} onAnimationEnd={handleIntroEnd} />
      <div className={`hidden-content ${introComplete ? "visible" : ""}`}>
        <div className="secretpage-banner">
          <Link to="/">
            <div className="home-btn">Home</div>
          </Link>
          <div className="letterpage-header">Poetry Corner</div>
          <div className="secret-subheader">I guess you finally found it</div>
          <div className="secret-subheader secret-body">
            These are some of my favorite pieces of poetry. There are also some
            that I wrote through different stages of my life. These are the only
            place where they are saved. Call it a poet's whim. Part of me wishes
            you find it when I am dead, that would be very poetic. But I liked
            sending you the little verses back in those days. So I figured I'll
            keep on doing that here.
          </div>
        </div>
        <SecretBoard />
        <Footer text={"Don't cry, you're perfect"} />
      </div>
    </div>
  );
}
