import React, { useState } from "react";
import "../styles/HomeStyle.scss";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
import { fontNames } from "../utils/constants";
import { useSocket } from "../contexts/SocketContext";
import axios from "axios";
import CircularProgress from "@mui/material/CircularProgress";

export default function PostButton({ limitPerPage }) {
  const [newLetter, setNewLetter] = useState({
    header: "",
    body: "",
    signature: "", // Constant value for now
    time: "", // Will be set when the post button is pressed
    font: fontNames[Math.floor(Math.random() * fontNames.length)],
  });

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isConfirmOpen, setIsConfirmOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const {socket, retrySocketConnection} = useSocket();

  // Update newLetter object when inputs change
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewLetter((prevState) => ({
      ...prevState,
      [name]: value, // Update the respective field based on its name
    }));
  };

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    if (!isConfirmOpen) {
      clearModal();
      setIsModalOpen(false);
    }
  };

  const openConfirm = () => {
    console.log("post btn clicked")
    setIsConfirmOpen(true); // Open the confirmation box
  };

  const closeConfirm = () => {
    setIsConfirmOpen(false); // Close the confirmation box without posting
  };

  const handlePost = () => {
    // Create options to format time and date
    console.log("confirm btn")
    setLoading(true);

    const letterToPost = {
      ...newLetter,
      time: new Date().getTime(), // Add the formatted time and date
    };

    if (socket) {
      socket.emit("newLetter", {
        newLetter: letterToPost,
        limit: limitPerPage,
      });
      console.log("Letter sent through socket");
    } else {
      axios
        .post(process.env.REACT_APP_API_URL + "/letters", letterToPost)
        .then((response) => {
          console.log("Letter posted through axios");
        })
        .catch((error) => {
          console.error("Error posting the letter:", error);
        });
    }

    // Close modal after posting
    closeConfirm();
    clearModal();
    setLoading(false);
    setIsModalOpen(false);
  };

  const clearModal = () => {
    setNewLetter({
      header: "",
      body: "",
      signature: "", // Constant value for now
      time: "", // Will be set when the post button is pressed
      font: fontNames[Math.floor(Math.random() * fontNames.length)],
    });
  };

  const CustomTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
      backgroundColor: "#173660",
      color: "#ffffff",
      boxShadow: theme.shadows[1],
      fontSize: 11,
    },
  }));

  return (
    <>
      {/* Floating Button */}
      <div className="floating-button" onClick={openModal}>
        <span className="plus-icon">+</span>
        <span className="button-text">Write a letter</span>
      </div>

      {/* Modal */}
      {isModalOpen && (
        <>
          <div className="modal-overlay"></div>
          <div className={`modal ${isConfirmOpen ? "non-interactable" : ""}`}>
            <span className="close-btn" onClick={closeModal}>
              ×
            </span>
            <input
              className="header-input"
              type="text"
              name="header"
              placeholder="Title"
              value={newLetter.header}
              onChange={handleInputChange}
              autoComplete="off"
            />
            <textarea
              className="body-input"
              name="body"
              placeholder="It's a letter, not a tweet! Let your words run free!"
              value={newLetter.body}
              onChange={handleInputChange}
              autoComplete="off"
            ></textarea>
            <div className="grid31">
              <input
                className="signature-input"
                type="text"
                name="signature"
                placeholder="Written by"
                value={newLetter.signature}
                onChange={handleInputChange}
                autoComplete="off"
              />
              <CustomTooltip
                title="Once posted, the letter can't be edited or recalled"
                placement="top"
              >
                <button
                  className="post-btn"
                  onClick={openConfirm}
                  disabled={
                    newLetter.header === "" ||
                    newLetter.body === "" ||
                    newLetter.signature === ""
                  }
                >
                  Post
                </button>
              </CustomTooltip>
            </div>
          </div>
        </>
      )}

      {/* Confirmation Box */}
      {isConfirmOpen && (
        <div className="confirm-box">
          {loading ? (
            <>
              <div className="confirm-message">
                Posting Letter
              </div>
              <div className="loading-icon">
                <CircularProgress
                  sx={{
                    color: "#173660",
                  }}
                />
              </div>
            </>
          ) : (
            <>
              <div className="confirm-message">
                Are you sure you want to post this letter?
              </div>
              <div className="confirm-actions">
                <button className="confirm-btn" onClick={handlePost}>
                  Yes
                </button>
                <button className="cancel-btn" onClick={closeConfirm}>
                  No
                </button>
              </div>
            </>
          )}
        </div>
      )}
    </>
  );
}
