import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import "../styles/HomeStyle.scss";
import { io } from "socket.io-client";
import "../styles/SendLetterPageStyle.scss";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
import { fontNames, MockImage, HER } from "../utils/constants";
import axios from "axios";
import CircularProgress from "@mui/material/CircularProgress";

export default function SecretSendPage() {
  const [newPoem, setNewPoem] = useState({
    header: "",
    body: "",
    signature: "", // Constant value for now
    time: "", // Will be set when the post button is pressed
    font: fontNames[Math.floor(Math.random() * fontNames.length)],
  });


  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isConfirmOpen, setIsConfirmOpen] = useState(false);
  const [messageVisible, setMessageVisible] = useState(false); // State for message visibility
  
  const [loading, setLoading] = useState(false);

  // Update newPoem object when inputs change
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewPoem((prevState) => ({
      ...prevState,
      [name]: value, // Update the respective field based on its name
    }));
  };

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    if (!isConfirmOpen) {
      clearModal();
      setIsModalOpen(false);
    }
  };

  const openConfirm = () => {
    setIsConfirmOpen(true); // Open the confirmation box
  };

  const closeConfirm = () => {
    setIsConfirmOpen(false); // Close the confirmation box without posting
  };

  const handlePost = () => {
    // Create options to format time and date
    setLoading(true);

    const poemToPost = {
      ...newPoem,
      time: new Date().getTime(), // Add the formatted time and date
    };

    axios
      .post(process.env.REACT_APP_API_URL+"/poems", poemToPost)
      .then((response) => {
        console.log("Letter posted successfully:", response.data);
        // Close modal after posting
        closeConfirm();
        clearModal();
        setIsModalOpen(false);

        // Show the message
        setMessageVisible(true);

        // Hide the message after 2 seconds
        setTimeout(() => {
          setMessageVisible(false);
        }, 2000);
      })
      .catch((error) => {
        console.error("Error posting the poem:", error);
      });
  };

  const clearModal = () => {
    setNewPoem({
      header: "",
      body: "",
      signature: "", // Constant value for now
      time: "", // Will be set when the post button is pressed
      font: fontNames[Math.floor(Math.random() * fontNames.length)],
    });
  };

  const CustomTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
      backgroundColor: "#173660",
      color: "#ffffff",
      boxShadow: theme.shadows[1],
      fontSize: 11,
    },
  }));

  return (
    <>
      <div className="sendletter-header">
        <div className="letter-head">Admin Use Only</div>
        {messageVisible && (
          <div className="post-message">Poem posted, thank you</div>
        )}
      </div>
      {/* Floating Button */}
      <div className="sendletter-btn" onClick={openModal}>
        Write a Poem
      </div>

      {/* Modal */}
      {isModalOpen && (
        <>
          <div className="modal-overlay"></div>
          <div className={`modal ${isConfirmOpen ? "non-interactable" : ""}`}>
            <span className="close-btn" onClick={closeModal}>
              ×
            </span>
            <input
              className="header-input"
              type="text"
              name="header"
              placeholder="Title"
              value={newPoem.header}
              onChange={handleInputChange}
              autoComplete="off"
            />
            <textarea
              className="body-input"
              name="body"
              placeholder="Body"
              value={newPoem.body}
              onChange={handleInputChange}
              autoComplete="off"
            ></textarea>
            <div className="grid31">
              <input
                className="signature-input"
                type="text"
                name="signature"
                placeholder="Written by"
                value={newPoem.signature}
                onChange={handleInputChange}
                autoComplete="off"
              />
              <CustomTooltip
                title="Once posted, the poem can't be edited or recalled"
                placement="top"
              >
                <button
                  className="post-btn"
                  onClick={openConfirm}
                  disabled={
                    newPoem.header === "" ||
                    newPoem.body === "" ||
                    newPoem.signature === ""
                  }
                >
                  Post
                </button>
              </CustomTooltip>
            </div>
          </div>
        </>
      )}

      
      {/* Confirmation Box */}
      {isConfirmOpen && (
        <div className="confirm-box">
          {loading ? (
            <>
              <div className="confirm-message">
                Posting Poem
              </div>
              <div className="loading-icon">
                <CircularProgress
                  sx={{
                    color: "#173660",
                  }}
                />
              </div>
            </>
          ) : (
            <>
              <div className="confirm-message">
                Are you sure you want to post this poem?
              </div>
              <div className="confirm-actions">
                <button className="confirm-btn" onClick={handlePost}>
                  Yes
                </button>
                <button className="cancel-btn" onClick={closeConfirm}>
                  No
                </button>
              </div>
            </>
          )}
        </div>
      )}
      
      <Link to="/">
        <div className="home-btn back-to-home">
          Back to Home
        </div>
      </Link>
    </>
  );
}
