import React, { useState, useEffect } from "react";
import "../styles/HomeStyle.scss";
import "../styles/SendLetterPageStyle.scss";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
import { fontNames, MockImage, HER } from "../utils/constants";
import axios from "axios";
import eclipse_img from "../media/eclipse.jpg";
import Intro from "../components/Intro";
import CircularProgress from "@mui/material/CircularProgress";

export default function SendLetterPage() {
  const [loading, setLoading] = useState(false);
  const [newLetter, setNewLetter] = useState({
    header: "",
    body: "",
    signature: "", // Constant value for now
    time: "", // Will be set when the post button is pressed
    font: fontNames[Math.floor(Math.random() * fontNames.length)],
  });

  // const title_img = MockImage;
  const title_img = eclipse_img;

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isConfirmOpen, setIsConfirmOpen] = useState(false);
  const [messageVisible, setMessageVisible] = useState(false); // State for message visibility
  const [introComplete, setIntroComplete] = useState(false);

  const handleIntroEnd = () => {
    setIntroComplete(true); // Show the main content after intro completes
  };

  // Update newLetter object when inputs change
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewLetter((prevState) => ({
      ...prevState,
      [name]: value, // Update the respective field based on its name
    }));
  };

  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    if (!isConfirmOpen) {
      clearModal();
      setIsModalOpen(false);
    }
  };

  const openConfirm = () => {
    setIsConfirmOpen(true); // Open the confirmation box
  };

  const closeConfirm = () => {
    setIsConfirmOpen(false); // Close the confirmation box without posting
  };

  const handlePost = () => {
    // Create options to format time and date
    setLoading(true);
    const letterToPost = {
      ...newLetter,
      time: new Date().getTime(), // Add the formatted time and date
    };

    axios
      .post(process.env.REACT_APP_API_URL+"/letters", letterToPost, {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((response) => {
        console.log("Letter posted successfully:", response.data);
        // Close modal after posting
        closeConfirm();
        clearModal();
        setLoading(false);
        setIsModalOpen(false);

        // Show the message
        setMessageVisible(true);

        // Hide the message after 2 seconds
        setTimeout(() => {
          setMessageVisible(false);
        }, 2000);
      })
      .catch((error) => {
        closeConfirm();
        clearModal();
        setLoading(false);
        setIsModalOpen(false);
        console.error("Error posting the letter:", error);
      });
  };

  const clearModal = () => {
    setNewLetter({
      header: "",
      body: "",
      signature: "", // Constant value for now
      time: "", // Will be set when the post button is pressed
      font: fontNames[Math.floor(Math.random() * fontNames.length)],
    });
  };

  const CustomTooltip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
      backgroundColor: "#173660",
      color: "#ffffff",
      boxShadow: theme.shadows[1],
      fontSize: 11,
    },
  }));

  return (
    <>
      <Intro tag={"Write A Letter"} onAnimationEnd={handleIntroEnd} />
      <div className={`hidden-content ${introComplete ? "visible" : ""}`}>
        <div className="sendletter-header">
          <div className="letter-head">{HER} is graduating!!!</div>
          <div className="card-bg">
            <div
              className="image"
              style={{ backgroundImage: `url(${title_img})` }}
            />
          </div>
          <div className="letter-prompt">
            Hey everyone, {HER} is graduating in December, so if you want to share a
            sweet memory you have with her from the past few years or just want
            to wish her the very best for the life ahead, you can do it down
            below (Before Dec 15). Thanks!
          </div>
          {messageVisible && (
            <div className="post-message">Letter posted, thank you</div>
          )}
        </div>
        {/* Floating Button */}
        <div className="sendletter-btn" onClick={openModal}>
          Write a letter
        </div>

        {/* Modal */}
        {isModalOpen && (
          <>
            <div className="modal-overlay"></div>
            <div className={`modal ${isConfirmOpen ? "non-interactable" : ""}`}>
              <span className="close-btn" onClick={closeModal}>
                ×
              </span>
              <input
                className="header-input"
                type="text"
                name="header"
                placeholder="Title"
                value={newLetter.header}
                onChange={handleInputChange}
                autoComplete="off"
              />
              <textarea
                className="body-input"
                name="body"
                placeholder="It's a letter, not a tweet! Let your words run free!"
                value={newLetter.body}
                onChange={handleInputChange}
                autoComplete="off"
              ></textarea>
              <div className="grid31">
                <input
                  className="signature-input"
                  type="text"
                  name="signature"
                  placeholder="Written by"
                  value={newLetter.signature}
                  onChange={handleInputChange}
                  autoComplete="off"
                />
                <CustomTooltip
                  title="Once posted, the letter can't be edited or recalled"
                  placement="top"
                >
                  <button
                    className="post-btn"
                    onClick={openConfirm}
                    disabled={
                      newLetter.header === "" ||
                      newLetter.body === "" ||
                      newLetter.signature === ""
                    }
                  >
                    Post
                  </button>
                </CustomTooltip>
              </div>
            </div>
          </>
        )}

        {/* Confirmation Box */}
        {isConfirmOpen && (
        <div className="confirm-box">
          {loading ? (
            <>
              <div className="confirm-message">
                Posting Letter
              </div>
              <div className="loading-icon">
                <CircularProgress
                  sx={{
                    color: "#173660",
                  }}
                />
              </div>
            </>
          ) : (
            <>
              <div className="confirm-message">
                Are you sure you want to post this letter?
              </div>
              <div className="confirm-actions">
                <button className="confirm-btn" onClick={handlePost}>
                  Yes
                </button>
                <button className="cancel-btn" onClick={closeConfirm}>
                  No
                </button>
              </div>
            </>
          )}
        </div>
      )}
      </div>
    </>
  );
}
