import { io } from "socket.io-client";

let socket;

export const initSocket = () => {
  if (!socket) {
    socket = io(process.env.REACT_APP_API_URL + "/", {
      transports: ["websocket", "polling"], // force WebSocket transport
    });
    // socket.on("connect", () => {
    //   console.log("Socket connected");
    // });
  }
  return socket;
};
