// src/context/SocketContext.js
import React, { createContext, useContext, useEffect, useState } from "react";
import { initSocket } from "../utils/socket";

const SocketContext = createContext(null);

export const useSocket = () => {
  return useContext(SocketContext);
};

export const SocketProvider = ({ children }) => {
  const [socket, setSocket] = useState(null);
  const [socketError, setSocketError] = useState(false);

  // useEffect(() => {
  //   const socketInstance = initSocket();
  //   setSocket(socketInstance);

  //   return () => {
  //     if (socket) socket.disconnect(); // Cleanup socket on component unmount
  //   };
  // }, []);

  const retrySocketConnection = () => {
    if (!socket) {
      const socketInstance = initSocket();
      setSocket(socketInstance);
    }
  };
  
  useEffect(() => {
    const connectSocket = () => {
      const socketInstance = initSocket();

      socketInstance.on("connect", () => {
        console.log("Socket connected On Mount");
        setSocket(socketInstance);
        setSocketError(false); // Reset error state on successful connection
      });

      socketInstance.on("connect_error", () => {
        console.error("WebSocket connection failed");
        setSocket(null);
        setSocketError(true);
      });

      return socketInstance;
    };

    // Try to connect the socket
    const socketInstance = connectSocket();

    // Cleanup on unmount
    return () => {
      if (socketInstance) socketInstance.disconnect();
    };
  }, [socketError]);


  return (
    <SocketContext.Provider value={{socket, retrySocketConnection}}>{children}</SocketContext.Provider>
  );
};
