import React, { useRef, useEffect, useState } from "react";
import { useSpring, animated, to } from "@react-spring/web";
import { useGesture } from "@use-gesture/react";
import { Link } from "react-router-dom";

import "../styles/DeckStyle.scss";

import { LazyLoadComponent } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';


const calcX = (y, ly) => -(y - ly - window.innerHeight / 2) / 40;
const calcY = (x, lx) => (x - lx - window.innerWidth / 2) / 120;

export default function Card({ img, alt, initRotZ, cardText, bgImg }) {
  const [zIndex, setZIndex] = useState(1); // Track z-index

  useEffect(() => {
    const preventDefault = (e) => e.preventDefault();
    document.addEventListener("gesturestart", preventDefault);
    document.addEventListener("gesturechange", preventDefault);

    return () => {
      document.removeEventListener("gesturestart", preventDefault);
      document.removeEventListener("gesturechange", preventDefault);
    };
  }, []);

  const target = useRef(null);

  // Spring animation for movement and rotation
  const [{ x, y, rotateX, rotateY, rotateZ, zoom, scale }, api] = useSpring(
    () => ({
      rotateX: 0,
      rotateY: 0,
      rotateZ: initRotZ,
      scale: 1,
      zoom: 0,
      x: 0,
      y: 0,
      config: { mass: 5, tension: 350, friction: 40 },
    })
  );

  // Gesture handling
  useGesture(
    {
      // Handle drag
      onDrag: ({ active, movement: [mx, my] }) => {
        // Update position during drag and reset after drag ends
        api.start({
          x: active ? mx : 0, // Smooth dragging based on movement
          y: active ? my : 0,
          scale: active ? 1.1 : 1, // Slightly enlarge when dragging
        });
      },

      // Handle mouse movement to update rotation based on position
      onMove: ({ xy: [px, py], dragging }) =>
        !dragging &&
        api.start({
          rotateX: calcX(py, window.innerHeight / 2),
          rotateY: calcY(px, window.innerWidth / 2),
        }),

      // Reset rotation and scale on hover end
      onHover: ({ hovering }) => {
        if (hovering) {
          // Straighten when hovering
          setZIndex(100);
          api.start({
            rotateX: 0,
            rotateY: 0,
            rotateZ: 0, // Straight position on hover
            scale: 1.35, // Slightly enlarged on hover
          });
        } else {
          setZIndex(1);
          api.start({
            rotateX: 0,
            rotateY: 0,
            rotateZ: initRotZ, // Original tilt rotation
            scale: 1, // Reset the scale
          });
        }
      },
      // Handle click event to straighten the card and return it after 2 seconds
      onClick: () => {
        setZIndex(100);
        // Rotate to straight
        api.start({
          rotateX: 0,
          rotateY: 0,
          rotateZ: 0, // Reset to straight
          scale: 1.35, // Slightly enlarge on click
        });

        // After 1 seconds, rotate back to original tilt
        setTimeout(() => {
          setZIndex(1);
          api.start({
            rotateX: 0, // You can calculate hover values here if needed
            rotateY: 0, // You can adjust this as per hover logic
            rotateZ: initRotZ, // Return to original rotation
            scale: 1, // Reset the scale
          });
        }, 2000); // 2-second delay
      },
    },
    { target, eventOptions: { passive: false } }
  );

  return (
    <div className="container" style={{ backgroundImage: `url(${bgImg})` }}>
      <div className="stack">
        {alt === "football2" && (
          <Link to="/secretPage" state={{ authorized: true }} style={{ textDecoration: "none" }}>
            <div className="secret-page-btn">Secret Page Button</div>
          </Link>
        )}
        <animated.div
          ref={target}
          className="card"
          style={{
            transform: "perspective(600px)",
            x,
            y,
            scale: to([scale, zoom], (s, z) => s + z),
            rotateX,
            rotateY,
            rotateZ,
            zIndex: zIndex,
          }}
        >
          <div className="card-content">
          <LazyLoadComponent>
            <div
              key={img}
              style={{ backgroundImage: `url(${img})` }}
              className="card-image"
            />
      </LazyLoadComponent>
            <div className="card-text">{cardText}</div>
          </div>
        </animated.div>
      </div>
    </div>
  );
}
