import React from "react";
import ReactDOM from "react-dom/client";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Home from "./pages/Home.js";
import "./styles/index.scss";
import LetterPage from "./pages/LetterPage.js";
import SendLetterPage from "./pages/SendLetterPage.js";
import SecretPage from "./pages/SecretPage.js";
import SecretSendPage from "./pages/SecretSendPage.js";
import ScrollToTop from "./utils/ScrollToTop.js";
import { SocketProvider } from "./contexts/SocketContext";

const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <SocketProvider>
      <Router>
        <ScrollToTop/>
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/sendLetter" element={<SendLetterPage />} />
          <Route path="/letterboard" element={<LetterPage />} />
          <Route path="/secretPage" element={<SecretPage />} />
          <Route path="/secretSend" element={<SecretSendPage />} />
        </Routes>
      </Router>
  </SocketProvider>
);
