import React, { useState } from "react";
import { Link } from "react-router-dom";
import Intro from "../components/Intro";
import Deck from "../components/Deck";
import Marquee from "../components/Marquee";
import { HER } from "../utils/constants";
import Footer from "../components/Footer";

export default function Home() {
  const [introComplete, setIntroComplete] = useState(false);

  const handleIntroEnd = () => {
    setIntroComplete(true); // Show the main content after intro completes
  };

  return (
    <div>
      <Intro tag={HER.toUpperCase()} onAnimationEnd={handleIntroEnd} />
      <div className={`hidden-content ${introComplete ? "visible" : ""}`}>
        <div className="homepage-banner">
          <div className="home-header">{HER.toUpperCase()}</div>
          <div className="center">
            <Link to="/letterboard" style={{ textDecoration: "none" }}>
              <div className="letterboard-btn">LetterBoard</div>
            </Link>
          </div>
        </div>
        {introComplete && <Marquee />}
        <Deck />
        <Footer text={"see you in 10 years"} />
      </div>
    </div>
  );
}
