import React, { useState, useEffect } from "react";
import "../styles/HomeStyle.scss";
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip";
import { styled } from "@mui/material/styles";
import useMediaQuery from "@mui/material/useMediaQuery"; 

export default function ShareButton() {
  const [shareMsg, setShareMsg] = useState(
    "Send this link to invite someone to post a letter"
  );
  const [tooltipOpen, setTooltipOpen] = useState(false); // State to control tooltip visibility

  // Replace with your custom link
  const customLink = `${process.env.REACT_APP_BASE_URL}/sendLetter`;

  // Use media query to detect if the user is on a mobile device
  const isMobile = useMediaQuery("(hover: none)");

  const handleShare = () => {
    // Copy the custom link to the clipboard
    navigator.clipboard.writeText(customLink);

    // Show the "Link copied" message
    setShareMsg("Link copied!!");
    // Show tooltip for mobile devices
    if (isMobile) {
      setTooltipOpen(true);
    }
    // Hide the message after 2 seconds
    setTimeout(() => {
      setShareMsg("Send this link to invite someone to post a letter");
      setTooltipOpen(false); // Hide tooltip after delay
    }, 2000);
  };

  const ShareToolTip = styled(({ className, ...props }) => (
    <Tooltip {...props} classes={{ popper: className }} />
  ))(({ theme }) => ({
    [`& .${tooltipClasses.tooltip}`]: {
      backgroundColor: "#173660",
      color: "#ffffff",
      boxShadow: theme.shadows[1],
      fontSize: 11,
    },
  }));

  return (
    <div>
      <ShareToolTip
        title={shareMsg}
        placement="top"
        open={tooltipOpen}
        disableHoverListener={isMobile} // Disable hover for mobile
        onClose={() => setTooltipOpen(false)} // Ensure tooltip closes
      >
        <div
          onClick={handleShare}
          onMouseEnter={!isMobile ? () => setTooltipOpen(true) : undefined} // Show on hover for desktop
          onMouseLeave={!isMobile ? () => setTooltipOpen(false) : undefined} // Hide on hover end for desktop
          className="home-btn share-btn"
        >
          Share
        </div>
      </ShareToolTip>
    </div>
  );
}
